import React, { Component } from 'react';
import moment from 'moment';
import { requestHeaderDELETEWithAuth, requestHeaderGETWithAuth, requestHeaderPOSTWithAuth } from 'src/Rest-API/Header';
import { getUserDataFromLocalStorage, logoutProcedureForLocalStorage } from 'src/Utils/AuthRelatedThings';
import { Logger, getSearchResult, openPageInNewTab, reloadPage, goHome } from '../../Utils/Utils';
import { Loading } from '../Statuses/Statuses';
import Table from 'react-bootstrap/Table';
import { CButton } from '@coreui/react';
import StatusModal from '../Modals/StatusModal';
import FailureModal from '../Modals/FailureModal';

const FILE_NAME = "WeightList.js";

const initialState = {
    isLoaded: false,
    loadingModalVisible: false,
    loadingModalState: 0,
    failureModalVisible: false,
    weightListData: []
};

class WeightList extends Component {

    constructor(props) {
        super(props);
        this.state = JSON.parse(JSON.stringify(initialState));
    }

    resetStateToDefault() {
        this.setState(JSON.parse(JSON.stringify(initialState)));
    }

    componentDidMount() {
        document.title = "Satanlabs - weight general page";
        this.fetchWeightDataList();
        
    }

    fetchWeightDataList() {
        let API_URL = 'https://backend.satanlabs.com/nexus/weight/list?offset=0&from=admin-portal';
        const userData = getUserDataFromLocalStorage();

        fetch(API_URL, requestHeaderGETWithAuth(userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        isLoaded: true,
                        weightListData: result.data
                    });
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );
    }

    reloadData() {
        this.resetStateToDefault();
        this.fetchWeightDataList();
    }

    markWeightEntityDeleted(id) {
        if(window.confirm("Surely want to DELETE weight.id = " + id + " ?")) {
            //call delete API and reload data
            this.callDeletionApi(id);
        }
    }

    callDeletionApi(id) {
        this.setState({
            loadingModalVisible: true,
            loadingModalState: 0,
        });

        const API_URL = "https://backend.satanlabs.com/nexus/weight/" + id;
        const userData = getUserDataFromLocalStorage();
        fetch(API_URL, requestHeaderDELETEWithAuth(null, userData.authToken))
        .then(response => {
            if(response.status === 200)
                return response.json();
            if(response.status === 401) {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
                setTimeout(() => {
                    logoutProcedureForLocalStorage();
                    reloadPage();
                }, 500);
            } else {
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
            throw new Error("HTTP status is " + response.status);
        })
        .then(
            (result) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "result", result);
                if(result.code === 1001) {
                    //success
                    this.setState({
                        loadingModalState: 1
                    });

                    setTimeout(() => {
                        this.setState({
                            loadingModalVisible: false,
                            loadingModalState: 0,
                        });
                        this.reloadData();
                    }, 500);
                } else {
                    //something horrible happened
                    this.setState({
                        loadingModalVisible: false,
                        loadingModalState: 0,
                        failureModalVisible: true
                    });
                }
            },
            (error) => {
                Logger(FILE_NAME, "callApi", "FETCH_1", "error", error);
                this.setState({
                    loadingModalVisible: false,
                    loadingModalState: 0,
                    failureModalVisible: true
                });
            }
        );

    }

    render() {
        Logger(FILE_NAME, "render", "", "this.state", this.state);

        return (
            this.state.isLoaded ? 
            <div>
                <h2>Weight data list : </h2>
                <CButton color="success" className="px-4" onClick={() => this.reloadData()}>reloadData</CButton>
                <br /><br />
                <Table striped bordered hover color='success' style={{'width':'100%'}}>
                    <thead>
                        <tr>
                        <th>#</th>
                        <th>Value</th>
                        <th>extraNotes</th>
                        <th>createdOn</th>
                        <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.weightListData.map(
                                weight => 
                                <tr key={"weight_item_id_" + weight.id}>
                                    <td>{weight.id}</td>
                                    <td>{weight.value}</td>
                                    <td>{weight.extraNotes == null ? "N/A" : weight.extraNotes}</td>
                                    <td>{weight.createdOn} ~ {moment(weight.createdOn).format('YYYY/MM/DD hh:mm ddd')}</td>
                                    <td>
                                        <CButton color="success" className="px-4" type='submit'>UPDATE</CButton>
                                            &nbsp;&nbsp;&nbsp;&nbsp;
                                        <CButton color="danger" className="px-4" onClick={() => this.markWeightEntityDeleted(weight.id)}>DELETE</CButton>
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                </Table>
                <StatusModal visible={this.state.loadingModalVisible} loadingModalState={this.state.loadingModalState} />
                <FailureModal visible={this.state.failureModalVisible} />
            </div>
             : <Loading />
        );
    }

}

export default WeightList;